@import url(https://fonts.googleapis.com/css?family=Quicksand&display=swap);
body {
  margin: 0;
  padding: 0;
  font-family:"Quicksand",-apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


.category-img { 
    width: 100%;
    opacity: 0.6;
    height: 220px;
}

.category-img:hover {
    opacity: 1;
    cursor: pointer;
}

.name-category { 
    margin: 10px;
    font-size: 18px;   
}

.ListCategory { 
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}

.all-category {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 15px;
    width: 100%;
    box-shadow: 10px 10px 10px rgb(177, 175, 175);
}

@media screen and (min-width : 300px) {
    
    .all-category {
        width: 75%;
    }
}

@media screen and (min-width : 600px) {
    
    .all-category {
        width: 45%;
    }
}

@media screen and (min-width : 900px) {
    
    .all-category {
        width: 30%;
    }
}

@media screen and (min-width : 1500px) {
    
    .all-category {
        width: 20%;
    }
}





.Nav {
    display: flex;
    justify-content: flex-end;
    background-color:#ffc800 ;
    padding: 5px 20px 5px 5px;
    position: fixed;
    width: 100%;
    z-index: 100;
}
.Nav h3 {
    color: white;
    text-decoration: none;
    cursor: pointer;
    margin-right: 30px;
}

.trainYourBrain:hover {
    color:#9da7cd;
}
.cardContent {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-bottom: 20px;
}

#cardContentBool {
    display : none;
}

#cardContentQcm {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-bottom: 20px;
}

.imageContainer {
    width: 100%;
    margin: auto;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}
.imageContainer figcaption {
    margin-top: 10px;
}

.imageCategory {
    max-width: 75%;
    
}

.ligneSection {
    color: #ffc800;
    height: 0.2em;
    background-color:  #ffc800;
    border: none;
    width: 40%;
}

.cardContent p {
    text-align: center;
    padding: 8 px;
}

.questionNumber {
    margin-top: 80px;
}


.qcmButton, #qcmButtonT, #qcmButtonF {
    border-radius: 15px;
    padding:  5px 10px 5px 10px;
    background-color: white;
    border: solid 2px blueviolet;
    margin: 5px;
    cursor: pointer;
    box-shadow:3px 3px 5px grey;
    word-wrap: wrap;
    color: black;
}


.qcmButton:hover {
    background-color: blueviolet !important;
    color: white !important;
}

.winButton, #winButtonT, #winButtonF {
    background-color: #ffc800;
    border: 2px #ffc800;
    margin: 5px;
    cursor: pointer;
    border-radius: 15px;
    padding:  5px 10px 5px 10px;
    box-shadow:3px 3px 5px grey;
    word-wrap: wrap;

}


.lostButton, #lostButtonT, #lostButtonF {
    border-radius: 15px;
    padding:  5px 10px 5px 10px;
    background-color: blueviolet;
    color: white;
    border: solid 2px blueviolet;
    margin: 5px;
    cursor: pointer;
    box-shadow:3px 3px 5px grey;
    word-wrap: wrap;

}

.winLogo , .lostLogo{
    width: 100%;
}

.imageWin {
    position: absolute;
    top: 80px;
    left:12px;
    transform: rotate(-20deg);

}

.imageLost {
    position:absolute;
    top: 80px;
    left:12px;
    transform: rotate(-20deg);
}
.noImage{
    display: none;
}
.button-order {
    display: grid;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
    grid-template-columns: 1fr 1fr;
}

.button-order :nth-child(2) { 
    order: 1; }
.button-order :nth-child(3) { 
    order: 2; }
.button-order :nth-child(4) { 
    order: 3; }

#cardContentQcm1 {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

}
.margin-choiceQcm {
    margin-top: 80px;
}
.question p {
    padding: 0 6px 0 6px ;
}

